import React from "react";
import {connect} from "react-redux";
import _ from "lodash";
import {
    getSelectedMaxMonthlyPriceRange,
    getSelectedOneTimePricesList,
    getSelectedProducer,
    getSelectedPrice,
    getSelectedFilter,
    getFilterConfiguration,
    getSelectedNumberFilter,
    getMatchToFilter,
    getMatchToDataList,
    getSelectedStickerFilter
} from "../../selectors";
import {
    setSelectedProducer,
    clearAttrsFilters,
    getFilteredProductList,
    changePriceFilter,
    changeStickerFilter,
    getFilteredProductTree,
    updateAttrsFilter,
    updateNumberAttrsFilter,
    updateMatchToFilters,
    reloadMatchToFilter,
    clearMatchToFilters,
    updateStickerAttrsFilter,
    clearOneTimePrices,
    clearMonthlyPrices
} from "eshop/modules/simfree/actions/filter";
import FilterUtils from "../../filterUtils"
import Tooltip from "../../../fix/components/fragments/Tooltip";
import {selectOneTimePrice} from "eshop/modules/simfree/actions/filter";

class ProductListHeaderFilterTagComponent extends React.Component {
    constructor(props) {
        super(props);
        console.log("Filters");
        console.log(this.props.filters);
    }

    clearDefault() {
        this.props.reloadTree();
        this.props.reloadMatchToFilter();

    }

    clearPriceFilter(type) {
        this.props.changePriceFilter(type, "");
        this.clearDefault();
    }

    clearProducer() {
        this.props.setSelectedProducer("");
        this.clearDefault();
    }

    clearFilterByKey(key) {
        this.props.updateFilter(FilterUtils.deleteFilterByCode(key, this.props.filters));
        this.clearDefault();
    }

    clearStickerFilterByKey(key) {
        this.props.updateStickerFilter(FilterUtils.deleteFilterByCode(key, this.props.selectedStickerFilter));
        this.clearDefault();
    }

    clearMatchFilterByKey(key) {
        this.props.updateMatchToFilter(FilterUtils.deleteMatchToFilterByCode(key, this.props.matchToFilter));
        this.clearDefault();
    }

    clearNumberFilterByKey(key) {
        this.props.updateNumberFilter(FilterUtils.deleteNumberFilterByCode(key, this.props.numberFilters));
        this.clearDefault();
    }

    clearOneTimePricesRange(id) {
        this.props.selectOneTimePrice(id);
    }

    clearMaxMonthlyPrices() {
        this.props.clearMonthlyPrices();
        this.clearDefault();
    }

    clearFilters() {

        this.props.changePriceFilter("from", "");
        this.props.changePriceFilter("to", "");
        this.props.clearMonthlyPrices();
        this.props.clearOneTimePrices();
        this.props.setSelectedProducer("");
        this.props.clearAttrFilters();
        this.props.clearMatchToFilters();

    }

    checkProducer() {
        return this.props.selectedProducer;
    }

    checkPriceFrom() {
        return this.props.selectedPrice && this.props.selectedPrice.from;
    }

    checkPriceTo() {
        return this.props.selectedPrice && this.props.selectedPrice.to;
    }

    isSelectedStickerFilterEmpty(stickerFilters) {
        return !_.find(this.props.filterConfiguration.stickerFilter, {code: stickerFilters});
    }

    checkFilterAttrs() {
        return Object.keys(this.props.filters).length > 0;
    }

    checkStickerFilterAttrs() {
        return Object.keys(this.props.selectedStickerFilter).length > 0;
    }

    checkMatchToFilters() {
        return this.props.matchToFilter && Object.keys(this.props.matchToFilter).length > 0;
    }

    checkStartPricesRangeFilters() {
        return this.props.selectedOneTimePrices && this.props.selectedOneTimePrices.length > 0;
    }

    checkMaxMonthlyPriceFilter() {
        return this.props.selectedMaxMonthlyPrice && this.props.selectedMaxMonthlyPrice.to;
    }

    checkNumberFilterAttrs() {
        var filter = this.props.numberFilters;
        if (!Object.keys(filter))
            return false;
        if (Object.keys(filter).length == 0)
            return false;
        var ok = false;
        Object.keys(filter).map(key => {
            if (filter[key] && filter[key] != "") {
                ok = true;
            }
        })
        return ok;
    }

    checkRenderFilter() {
        return this.checkMaxMonthlyPriceFilter() || this.checkStartPricesRangeFilters() || this.checkStickerFilterAttrs() || this.checkProducer() || this.checkPriceFrom() || this.checkPriceTo() || this.checkFilterAttrs() || this.checkNumberFilterAttrs() || this.checkMatchToFilters();
    }

    render() {
        if (this.checkRenderFilter()) {
            return (
                <div id={'opl-tags-filter-header'}>

                    {this._renderFilterBoxes()}
                    <a href="#!" className="opl-tags__deleteAll" onClick={this.clearFilters.bind(this)}>Usuń wszystkie
                        filtry</a>
                </div>
            )
        } else {
            return null;
        }
    }

    _renderFilterBoxes() {
        return (
            <u className="opl-tags__list ui-sortable">
                {this._renderProducerFilter()}
                {this._renderPriceFromFilter()}
                {this._renderStartPriceRangeFilter()}
                {this._renderMaxMonthlyPriceFilter()}
                {this._renderPriceToFilter()}
                {this._renderFilterConfiguration()}
                {this._renderNumberFilterConfiguration()}
                {this._renderMatchToFilter()}
                {this._renderStickerFilter()}
            </u>
        )
    }

    _renderProducerFilter() {
        if (this.checkProducer()) {
            return this._renderNameValueFilterBadge('Producent', this.props.selectedProducer, this.clearProducer.bind(this))
        } else {
            return null;
        }
    }

    _renderMatchToFilter() {
        var matchToFilters = this.props.matchToFilter;
        return Object.keys(matchToFilters).map((key) => {
            if (key && key != "") {
                return this._renderNameValueFilterBadge('Pasuje do', FilterUtils.getMatchToDescription(key, this.props.matchToFilterData), this.clearMatchFilterByKey.bind(this, key));
            }
        });

    }

    _renderPriceFromFilter() {
        if (this.checkPriceFrom()) {
            return this._renderNameValueFilterBadge('Cena Od', this.props.selectedPrice.from, this.clearPriceFilter.bind(this, 'from'));
        } else {
            return null;
        }
    }

    _renderPriceToFilter() {
        if (this.checkPriceTo()) {
            return this._renderNameValueFilterBadge('Cena Do', this.props.selectedPrice.to, this.clearPriceFilter.bind(this, 'to'));
        } else {
            return null;
        }
    }

    _renderStartPriceRangeFilter() {
        //per id
        if (this.checkStartPricesRangeFilters()) {
            return this.props.selectedOneTimePrices.map(range =>
                this._renderNameValueFilterBadge('Opłata na start', range.from + '-' + range.to + ' zł', this.clearOneTimePricesRange.bind(this, range.id))
            );
        } else {
            return null;
        }
    }

    _renderMaxMonthlyPriceFilter() {
        if (this.checkMaxMonthlyPriceFilter()) {
            return this._renderNameValueFilterBadge('Miesięcznie do', this.props.selectedMaxMonthlyPrice.to + " zł", this.clearMaxMonthlyPrices.bind(this));
        } else {
            return null;
        }
    }

    _renderFilterConfiguration() {
        var filter = this.props.filters;
        console.log(filter);
        return Object.keys(filter).map(key => {
            return this._renderNameValueFilterBadge(FilterUtils.getFilterNameDescription(key, this.props.filterConfiguration.categoryFilter), filter[key], this.clearFilterByKey.bind(this, key));
        });

    }

    _renderNumberFilterConfiguration() {
        var filter = this.props.numberFilters;
        return Object.keys(filter).map(key => {
            if (filter[key] && filter[key] != "") {
                return this._renderNameValueFilterBadge(FilterUtils.getNumberFilterNameDescription(key, this.props.filterConfiguration.categoryFilter), filter[key], this.clearNumberFilterByKey.bind(this, key));
            }
        });

    }

    _renderStickerFilter() {
        if (this.checkStickerFilterAttrs()) {
            var stickerFilters = this.props.selectedStickerFilter;
            console.log("_renderStickerFilter EEEEEEEEEEEEEEEEEEEEEEe");
            console.log(stickerFilters);
            return Object.keys(stickerFilters).map(key => {
                return this._renderNameValueFilterStickerBadge(key, stickerFilters[key], this.clearStickerFilterByKey.bind(this, key));
            })

        } else {
            return null;
        }
    }

    _renderNameValueFilterStickerBadge(name, value, onClickHandler) {
        if (this.isSelectedStickerFilterEmpty(name)) {
            return this._renderFilterStickerWithTooltip(value, onClickHandler)
        } else {
            return this._renderFilterSticker(value, onClickHandler);
        }
    }

    _renderFilterSticker(value, onClickHandler) {
        return (
            <li className="opl-tags__item" key={value}>
                <span>
                    <b>{this.stripHtmlTagsFromString(value)}</b>
                </span>
                <a href="#!" className="opl-tags__delete" onClick={onClickHandler}>
                    <span className="g-icon g-icon--only g-icon--xs g-icon--close" aria-hidden="true"></span>
                    <span className="u-acc-hide">Usuń</span>
                </a>
            </li>);
    }

    _renderFilterStickerWithTooltip(value, onClickHandler) {
        return (
            <li className="opl-tags__item g-white1-c" key={value}>
                <Tooltip maxWidth="300">
                  <span className="o-tooltip__trigger o-tooltip--top">
                      <b>{this.stripHtmlTagsFromString(value)}</b>
                      <span className="o-tooltip__content">
                          ten filtr jest nieaktywny,nie ma dla niego żadnego urządzenia
                      </span>
                  </span>
                </Tooltip>
                <a href="#!" className="opl-tags__delete" onClick={onClickHandler}>
                    <span className="g-icon g-icon--only g-icon--xs g-icon--close" aria-hidden="true"></span>
                    <span className="u-acc-hide">Usuń</span>
                </a>
            </li>);
    }

    _renderNameValueFilterBadge(name, value, onClickHandler) {
        return (
            <li className="opl-tags__item" style={{display: 'list-item', opacity: 1}} key={name + "_" + value}>
                <span> {name} </span>
                <span><b>{this.stripHtmlTagsFromString(value)}</b></span>
                <a href="#!" className="opl-tags__delete" onClick={onClickHandler}>
                    <span className="g-icon g-icon--only g-icon--xs g-icon--close" aria-hidden="true"></span>
                    <span className="u-acc-hide">Usuń</span>
                </a>
            </li>
        );
    }

    stripHtmlTagsFromString(text) {
        return text.replace(/<\/?[^>]+(>|$)/g, "");
    }
}

const mapStateToProps = state => ({
    selectedProducer: getSelectedProducer(state),
    selectedPrice: getSelectedPrice(state),
    matchToFilter: getMatchToFilter(state),
    matchToFilterData: getMatchToDataList(state),
    filters: getSelectedFilter(state),
    numberFilters: getSelectedNumberFilter(state),
    filterConfiguration: getFilterConfiguration(state),
    selectedStickerFilter: getSelectedStickerFilter(state),
    selectedOneTimePrices: getSelectedOneTimePricesList(state),
    selectedMaxMonthlyPrice: getSelectedMaxMonthlyPriceRange(state),
});

const mapDispatchToProps = (dispatch) => ({
    reloadTree: (page) => dispatch(getFilteredProductTree(page)),
    setSelectedProducer: (selectedProducer) => dispatch(setSelectedProducer(selectedProducer)),
    clearAttrFilters: () => dispatch(clearAttrsFilters()),
    clearMatchToFilters: () => dispatch(clearMatchToFilters()),
    getFilteredList: (page) => dispatch(getFilteredProductList(page)),
    changePriceFilter: (type, price) => dispatch(changePriceFilter(type, price)),
    changeStickerFilter: (value) => dispatch(changeStickerFilter(value)),
    updateFilter: (actualFilter) => dispatch(updateAttrsFilter(actualFilter)),
    updateStickerFilter: (actualStickerAttrFilters) => dispatch(updateStickerAttrsFilter(actualStickerAttrFilters)),
    updateNumberFilter: (actualNumberFilter) => dispatch(updateNumberAttrsFilter(actualNumberFilter)),
    updateMatchToFilter: (matchToFilter) => dispatch(updateMatchToFilters(matchToFilter)),
    reloadMatchToFilter: () => dispatch(reloadMatchToFilter()),
    clearMonthlyPrices: () => dispatch(clearMonthlyPrices()),
    clearOneTimePrices: () => dispatch(clearOneTimePrices()),
    selectOneTimePrice: (id) => dispatch(selectOneTimePrice(id))
});

export default connect(mapStateToProps, mapDispatchToProps)(ProductListHeaderFilterTagComponent)